import { useState } from 'react';
import { FaAngleDoubleRight, FaChevronRight } from 'react-icons/fa';
// , buyerData, sellerData, onInputChange, onInputChange
function BuyerorSeller({onNext,formData,onInputChange}) {
    const handleSubmit = (e)=>{
        e.preventDefault()
        onNext()
    }
    return ( 
        <div className='modalDetails'>
            <form onSubmit={handleSubmit} action="">
                {/* <div className="modalForm">
                    <label htmlFor="buyername">Buyer name</label>
                    <input type="text" name="buyername" id="buyername" value={formData.buyername} onChange={onInputChange} />
                </div> */}
                <div className="modalForm">
                    <label htmlFor="sellername">Seller name</label>
                    <input type="text" required name="seller.name" id="seller.name" value={formData.seller.name} onChange={onInputChange} />
                </div>
                {/* <div className="modalForm">
                    <label htmlFor="buyerEmail">Buyer Email</label>
                    <input type="email" name="buyerEmail" id="buyerEmail" value={formData.buyerEmail} onChange={onInputChange} />
                </div> */}
                <div className="modalForm">
                    <label htmlFor="selleremail">Seller Email</label>
                    <input required type="email" 
                        name="seller.email" 
                        id="seller.email" 
                        value={formData.seller.email} 
                        onChange={onInputChange} />
                </div>
                {/* <div className="modalForm">
                    <label htmlFor="buyerPhone">Buyer phone number</label>
                    <input type="phone" name="buyerPhone" id="buyerPhone" value={formData.buyerPhone} onChange={onInputChange} />
                </div>
                <div className="modalForm">
                    <label htmlFor="sellerphone">Buyer phone number</label>
                    <input type="phone" name="sellerphone" id="sellerphone" value={formData.sellerphone} onChange={onInputChange} />
                </div>
                <div className="modalForm">
                    <label htmlFor="buyerid">Buyer ID</label>
                    <input type="text" name="buyerid" id="buyerid" value={formData.buyerid} onChange={onInputChange} />
                </div> */}
                <div className="modalForm">
                    <label htmlFor="sellerid">Seller ID</label>
                    <input type="text" name="seller.id" id="seller.id" value={formData.seller.id} onChange={onInputChange} />
                </div>
                
                {/* Shipping Method: */}
                <div className="modalForm">
                    <label htmlFor="shipping">Shipping Method*</label>
                    <select
                        name="shipping"
                        id="shipping"
                        required
                        value={formData.shipping}
                        onChange={onInputChange}
                    >
                        <option value="">Select</option>
                        <option value="Road">Road</option>
                        <option value="Rail">Rail</option>
                        <option value="Airfreight">Airfreight</option>
                        <option value=" ContainerShipping">Container Shipping</option>
                    </select>
                </div>

                 {/* TRANSACTION TYPE */}
                 <div className="modalForm">
                    <label htmlFor="TransactionType">Transaction Type</label>
                    <select
                        name="TransactionType"
                        id="TransactionType"
                        value={formData.TransactionType}
                        onChange={onInputChange}
                    >
                        <option value="">Select</option>
                        <option value=",one-off">One-off</option>
                        <option value="milestone">Milestone</option>
                        <option value="layby">Layby</option>
                    </select>
                </div>
               {/* CATEGORY */}
               <div className="modalForm">
                    <label htmlFor="customerRole">Category*</label>
                    {formData.category != 'others' ? 
                    
                    <select
                        name="category"
                        id="category"
                        required
                        value={formData.category}
                        onChange={onInputChange}
                    >
                        <option value="">Select</option>
                        <option value="Paddy Rice">Paddy Rice</option>
                        <option value="Cashew Nut">Cashew Nut</option>
                        <option value="Maize">Maize</option>
                        <option value="Beans">Beans</option>
                        <option value="Cocoa">Cocoa</option>
                        <option value="Sesame Seed">Sesame Seed</option>
                        <option value="Ginger">Ginger</option>
                        <option value="others">Others</option>
                    </select>
                    :
                    <input
                    type="text"
                    placeholder="please enter category"
                    name="othercategory"
                    id="othercategory"
                    // value={formData.categoryInput}
                    onChange={onInputChange}
                    required
                  />
                }
                </div>

                {/* QUANTITY */}
                <div className="modalForm">
                    <label htmlFor="commodityQuantity">Commodity Quantity <small>in Metric Ton</small>*</label>
                    <input
                        required
                        type="text"
                        name="quantity"
                        id="quantity"
                        value={formData.quantity}
                        onChange={onInputChange}
                    />
                </div>
                <button  className='detailsbtn'><FaAngleDoubleRight />Proceed to Summary</button>
            </form>
        </div>
     );
}

export default BuyerorSeller;
