import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit,faUser,faFileAlt,faFile,faCalendar,faAddressBook, faMoneyBill1} from "@fortawesome/free-regular-svg-icons";
import { faCartShopping } from "@fortawesome/free-solid-svg-icons";
import { useEffect, useState } from 'react';
import { FaChartLine } from "react-icons/fa";

function UserDetails() {

    return ( 
        <div  className="userAccountDetails">
                <div className="wallet detailCOntainer">
                    <p>Wallet</p>
                    <div className="wallletDetails innerCon">
                        <span className="remove"><FontAwesomeIcon icon={faMoneyBill1} color='white' size="lg"/> </span>
                        <div className="amounts">
                            <p>Available balance</p>
                            <h3>₦400,000</h3>
                        </div>
                    </div>
                </div>
                <div className="totalOrders detailCOntainer">
                    <p>Total orders</p>
                    <div className="orderDetails innerCon">
                         <span className=" remove"><FontAwesomeIcon icon={faCartShopping} color='white' size="lg"/> </span>
                        <div className="amounts">
                            <p>24 Orders</p>
                            <p>₦400,000</p>
                        </div>
                         {/* <FontAwesomeIcon className="chartIcon remove" icon={faUser} /> */}
                         <div style={{display: 'flex', justifyContent: 'space-between', width: 'fit-content'}}>
                         <FaChartLine color='green' className="remove chartIcon" size={22}/>
                            <p style={{color: 'green'}}>12.1%</p>
                         </div>
                    </div>
                </div>
                <div className="totalOrders detailCOntainer">
                    <p>  <span className="remove">Commodities</span>Sold</p>
                    <div className="orderDetails innerCon">
                         <span className="remove"><FontAwesomeIcon icon={faFileAlt} color='white' size="lg"/> </span>
                        <h2>240</h2>
                         {/* <FontAwesomeIcon className="remove chartIcon" icon={faFile} color='green'/> */}
                         <div style={{display: 'flex', justifyContent: 'space-between', width: 'fit-content'}}>
                         <FaChartLine color='green' className="remove chartIcon" size={22}/>
                            <p style={{color: 'green'}}>12.1%</p>
                         </div>

                        
                    </div>
                </div>
                <div className="totalOrders detailCOntainer">
                    <p > <span className="remove">Commodities</span>  Bought</p>
                    <div className="orderDetails innerCon">
                        <div style={{display: 'flex',gap:'.2rem', alignItems: 'center'}}>
                            <span className="remove"><FontAwesomeIcon icon={faFileAlt} color='white' size="lg"/> </span>
                            <h2>240</h2>
                        </div>
                        {/* <FontAwesomeIcon className="remove chartIcon" icon={faFile} color='green'/> */}
                        <div style={{display: 'flex', justifyContent: 'space-between', width: 'fit-content'}}>
                            <FaChartLine className="remove chartIcon" size={22} color="red"  />
                            <p style={{color: 'red'}}>12.1%</p>
                        </div>

                    </div>
                </div>
        </div>
     );
}

export default UserDetails;