import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import { useUpdateUserMutation } from "context/usersApiSlice";
import { setCredentials } from "context/authSlice";

function ProfileSet() {
  const { register, handleSubmit, watch, setValue } = useForm();
  const [imageUpload, setImageUpload] = useState(null);


const dispatch = useDispatch();
  const { userInfo } = useSelector((state) => state.auth);

  const [updateUser, {isLoading}] = useUpdateUserMutation();

  useEffect(() => {
    // Set initial form values
    if (userInfo) {
      const { id, firstName, lastName, email } = userInfo.user;
      setValue("id", id);
      setValue("firstName", firstName);
      setValue("lastName", lastName);
      setValue("email", email);
    }
  }, [userInfo, setValue]);

  const onSubmit = async (data) => {
    data.imageUpload = imageUpload; // Add imageUpload to the payload
    console.log(data);
    try {
      const res = await updateUser(data).unwrap();
      dispatch(setCredentials({...res}))
      toast.success('Profile Updated')
    } catch (error) {
      toast.error(error.data.message || error.error);
    }
  };

  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    const fileType = file.type;
    const validImageTypes = ["image/jpeg", "image/png", "image/gif"];

    if (!validImageTypes.includes(fileType)) {
      toast.error("Enter a valid image format");
      return;
    }

    setImageUpload(URL.createObjectURL(file));
  };

  return (
    <div className="profile">
      <h3 style={{fontSize: '1.2rem', color: '#003918', fontWeight: '500'}}>Edit Profile</h3>
      <div className="profileElemenet">
        <p>Profile photo</p>
        <div className="detailsss addimagediv">
          <div className="addedimage">
            <input
              className="input_file"
              id="file-input"
              type="file"
              onChange={handleImageUpload}
            />
            {imageUpload && (
              <img
                className="addImage"
                accept="image/*"
                src={imageUpload}
                alt="profile image"
              />
            )}
          </div>
        </div>
        <form onSubmit={handleSubmit(onSubmit)} className="profileform">
          <div className="input">
            <label htmlFor="credenceId">Credence ID</label>
            <input
              type="text"
              id="id"
              name="id"
              disabled
              {...register("id")}
            />
          </div>
          <div className="input">
            <label htmlFor="Name">First Name</label>
            <input
              type="text"
              id="firstName"
              name="firstName"
              {...register("firstName")}
            />
          </div>
          <div className="input">
            <label htmlFor="lastName">Last Name</label>
            <input
              type="text"
              id="lastName"
              name="lastName"
              {...register("lastName")}
            />
          </div>
          <div className="input">
            <label htmlFor="email">Email address</label>
            <input
              type="email"
              id="email"
              name="email"
              {...register("email")}
            />
          </div>
          <div className="input">
            <label htmlFor="phone">Phone Number</label>
            <input
              type="number"
              id="phone"
              name="phone"
              {...register("phone")}
            />
          </div>
          <div className="input">
            <label htmlFor="phone">NIN</label>
            <input
              type="number"
              id="nin"
              name="nin"
              {...register("nin")}
            />
          </div>
          <div className="input">
            <label htmlFor="Trade">Trade</label>
            <select id="Trade" name="Trade" {...register("Trade")}>
              <option value="">Select Trade</option>
              <option value="farmer">Farmer</option>
              <option value="commodityTrader">Commodity Trader</option>
              <option value="foodProcessor">Food Processor</option>
              <option value="retailer">Retailer</option>
              <option value="other">Other</option>
            </select>
          </div>
          {watch("Trade") === "other" && (
            <div className="input">
              <label htmlFor="otherTrade">Enter Your Trade:</label>
              <input
                type="text"
                id="otherTrade"
                name="otherTrade"
                {...register("otherTrade")}
              />
            </div>
          )}
          <button disabled={isLoading} type="submit" className="btns profilebtns">
            {isLoading ? 'Updating...' : 'Update'}
          </button>
        </form>
      </div>
    </div>
  );
}

export default ProfileSet;
