import axios from 'axios';
import { addToTransaction } from 'context/transactionslice';

import { useCreateTransactionMutation } from 'context/usersApiSlice';
import { FaEdit, FaRegEdit } from 'react-icons/fa';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';

const bg = require('assets/profile.png')

function TransactionSummary({onNext, imageUploads,handleImageUpload, formData}) {
 const price = parseInt(formData.price)
 const Escrowprice = price * 0.05;
 const total = price + Escrowprice;
 
 const deliveryDate = new Date(formData.deliveryDate).toLocaleDateString('en-GB', {
  day: 'numeric',
  month: 'short',
  year: 'numeric'
});

    const [createTransaction, { isLoading }] = useCreateTransactionMutation();
    const { userInfo } = useSelector((state) => state.auth);
    const dispatch = useDispatch();
    const token = userInfo.user.token;



    formData.buyer.name = userInfo.user.firstName + userInfo.user.lastName;
    formData.buyer.email = userInfo.user.email;
    formData.buyer.id = userInfo.user.id;
    formData.buyer.phone = userInfo.user.phone;

    const handleSubmit = async()=>{
      const data = formData;
      console.log(data)

      // try {
      //   const res = await createTransaction(data, token).unwrap();
      //   dispatch(setCredentials({...res}))

      // } catch (error) {
      //   toast.error(error.data.message || error);
      // }
      try {
        var resp = await  axios.post('https://credenceapp-api.onrender.com/transaction/create',data, {
          headers: {
            'Authorization': `Bearer ${token}` 
           } 
        })
        onNext()
        toast.success(resp.data.message)
        // dispatch(addToTransaction(resp.config.data))
      } catch (error) {
          // toast.error(error.data.message || error.error);

      }
    }
  return (
    <div className="summarydiv modalDetails">
      <div className="summarydetails">
        <div className="detailsss">
          <h6>Title:</h6>
          <h1>{formData.tittle}</h1>
          {/* <input type="text" name="title" value={formData.title} onChange={handleChange} /> */}
        </div>
        <div className="detailsss">
          <p>Category:</p>
          <h1>{formData.category === 'others' ? formData.othercategory : formData.category}</h1>
          {/* <input type="text" name="category" value={formData.category} onChange={handleChange} /> */}
        </div>
        <div className="detailsss">
          <p>Quantity:</p>
          <h1>{formData.quantity}</h1>
          {/* <input type="text" name="quantity" value={formData.quantity} onChange={handleChange} /> */}
        </div>
        <div className="detailsss">
          <p>Delivery date:</p>
          <h1>{formData.delivery_date}</h1>
          {/* <input type="text" name="delivery" value={formData.delivery} onChange={handleChange} /> */}
        </div>
        {/* <div className="detailsss">
          <p>Inspection Duration:</p>
          <h1>{formData.inspecctionDuration}</h1>
        </div> */}
        {/* <div className="detailsss addimagediv">
          <p>Add images</p>
          <div className='addedimage'>
          <input id="file-input" type="file" onChange={handleImageUpload} multiple/>
          {imageUploads.map((imageDataUrl) => (
          <img key={imageDataUrl} className="addImage" accept="image/*" src={imageDataUrl} alt="product image" />
        ))}
          </div>
        </div> */}
      </div>
      <div className="contact_details">
        <div className='big_contact_detail_div'>
          <div className="contact_detail">
            <p>To</p>
            <h3>{formData.seller.name}</h3>
          </div>
          <div className="contact_detail">
            <p>Email</p>
            <h3>{formData.seller.email}</h3>
          </div>
          <div className="contact_detail">
            <p>Price(subtotal)</p>
            <h1>{price}</h1>
          </div>
          <div className="contact_detail">
            <p>Escrow fee(5%)</p>
            <h1>{Escrowprice}</h1>
          </div>
        </div>
        <div className='big_total_contact_detail_div'>
          <div className="contact_detail divider">
            <p><i>All prices are in NGN. Taxes may apply</i></p>
            <hr />
            <hr />
          </div>
          <div className="contact_detail totaldiv">
            <h2>Total:</h2>
            <h2>{total}</h2>
          </div>
        </div>
      </div>
      {/* <NavLink to="/transactionPreview/buyerPay"> */}

        <button onClick={handleSubmit} disabled={isLoading} type='submit' className='summarydetailsbtn detailsbtn'><FaRegEdit size='1.5rem'/> {isLoading ? 'Creating your transaction' : 'Create Transaction'}</button>
      {/* </NavLink> */}
    </div>
  );
}

export default TransactionSummary;
