import { faMoneyBill1 } from "@fortawesome/free-regular-svg-icons";
import { faCartShopping, faCog, faDashboard, faSignOutAlt, faToolbox } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState, useEffect } from "react";
import { FaSignOutAlt } from "react-icons/fa";
import { useDispatch } from "react-redux";
import {logout} from '../context/authSlice'
import { Link, useLocation, useNavigate } from "react-router-dom";
const myImage = require("../assets/Frame 58 (1).png");
const Logo = require("../assets/Union (1).png");

function Sidenav() {
  const [activeLink, setActiveLink] = useState("dashboard");
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const location = useLocation();
  const handleLinkClick = (link)=>{
    setActiveLink(link)
  }
  
  useEffect(() => {
    // Update activeLink when route changes
    const pathname = location.pathname;
    if (pathname === "/admin/dashboard") {
      setActiveLink("dashboard");
    } else if (pathname === "/admin/transactions") {
      setActiveLink("transaction");
    } else if (pathname === "/admin/payment") {
      setActiveLink("pay");
    } else if (pathname.startsWith("/admin/settings")) {
      setActiveLink("settings");
    }
  }, [location]);
  const handleLogout = async () => {
    // localStorage.removeItem("token");
    try {
      // await logoutApiCall().unwrap();
      dispatch(logout())
      navigate("/reg/login");
    } catch (error) {
      console.log(error)
    }
  };
  return (
    <header>
      <nav>
        <div className="brand sidebrand">
          <img src={Logo} alt="" />
          <img src={myImage} alt="" />
        </div>
        <ul className='navigation navigations'>
          <li className={activeLink === "dashboard" ? "active" : ""}>
            {/* <img src={Dash} alt="" /> */}
            <FontAwesomeIcon className="navIcon" color='white' size="2x" icon={faDashboard}/>
            <Link to="/admin/dashboard" onClick={() => handleLinkClick("dashboard")}>
              Dashboard
            </Link>
          </li>
          <li className={activeLink === "transaction" ? "active" : ""}>
            {/* <img src={transact} alt="" /> */}
            <FontAwesomeIcon className='navIcon' color='white' size="2x" icon={faCartShopping}/>
            <Link to="/admin/transactions" onClick={() => handleLinkClick("transaction")}>
              Transactions
            </Link>
          </li>
          <li className={activeLink === "pay" ? "active" : ""}>
            {/* <img src={pay} alt="" /> */}
            <FontAwesomeIcon className='navIcon' color='white' size="2x" icon={faMoneyBill1
            }/>
            <Link to="/admin/payment" onClick={() => handleLinkClick("pay")}>
              Payment
            </Link>
          </li>
          <li className={activeLink === "settings" ? "active" : ""}>
            {/* <img src={settingImg} alt="" /> */}
            <FontAwesomeIcon className='navIcon' color='white' size="2x" icon={faCog}/>
            <Link to="/admin/settings/profile" onClick={() => handleLinkClick("settings")}>
              Settings
            </Link>
          </li>
          <li>
            {/* <img src={settingImg} alt="" /> */}
            <FaSignOutAlt  color="white" size={'35px'}/>
            <Link onClick={handleLogout}>
              Logout
            </Link>
          </li>
          
        </ul>
      </nav>
    </header>
  );
}

export default Sidenav;
