import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { FaChevronRight } from 'react-icons/fa';
import { Form } from 'react-router-dom';

function Details({onNext, formData,onInputChange}) {
    // console.log(formData, 'step 1')

      const handleSubmit = (e) => {
        e.preventDefault();
        onNext();
      };
    return ( 
        <div className='modalDetails'>
            <form onSubmit={handleSubmit} action="">
                <div className="modalForm">
                    <label htmlFor="transactTitle">Transaction title*</label>
                    <input
                        type="text"
                        // name="transactTitle"
                        name="tittle"
                        required
                        id="tittle"
                        // id="transactTitle"
                        value={formData.tittle}
                        onChange={onInputChange}
                    />
                </div>
                {/* <div className="modalForm">
                    <label htmlFor="customerRole">Customer Role*</label>
                    <select
                        name="customerRole"
                        id="customerRole"
                        required
                        value={formData.customerRole}
                        onChange={onInputChange}
                    >
                        <option value="">Select</option>
                        <option value="Buyer">Buyer</option>
                        <option value="Seller">Seller</option>
                    </select>
                </div> */}
                {/* INSPECTION DURATION */}
                <div className="modalForm">
                    <label htmlFor="inspecctionDuration">Inspection Duration*</label>
                    <input
                        type="number"
                        name="inspecctionDuration"
                        id="inspecctionDuration"
                        placeholder="Inspection Duration in day(s)"
                        required
                        value={formData.inspecctionDuration}
                        onChange={onInputChange}
                    />
                </div>


                {/* GRADING PARAMETER */}
                <div className="modalForm">
                    <label htmlFor="grading">Grading Parameter</label>
                    <select
                        name="grading"
                        id="grading"
                        value={formData.grading}
                        onChange={onInputChange}
                    >
                        <option value="">Select</option>
                        <option value="MoistureContent">Moisture Content</option>
                        <option value="KOR">KOR</option>
                        <option value="SeedCount">Seed Count</option>
                        <option value="infestation">infestation</option>
                        <option value="ForeignMatters">Foreign Matters</option>
                    </select>
                </div>    

                {/* SHIPPING FEE PAID BY */}
                <div className="modalForm">
                    <label htmlFor="shippingFee">Shipping fee paid by:*</label>
                    <select
                        name="shippingFee"
                        id="shippingFee"
                        required
                        value={formData.shippingFee}
                        onChange={onInputChange}
                    >
                        <option value="">Select</option>
                        <option value="Buyer">Buyer</option>
                        <option value="Seller">Seller</option>
                    </select>
                </div>

                <div className="modalForm">
                    <label htmlFor="commodityName">Commodity Name*</label>
                    <input
                        type="text"
                        name="commodityName"
                        id="commodityName"
                        required
                        value={formData.commodityName}
                        onChange={onInputChange}
                    />
                </div>

                <div className="modalForm">
                    <label htmlFor="date-picker">Delivery date*</label>
                    <input 
                        type="date"
                        name='delivery_date'
                        id='delivery_date'
                        required
                        dateformat="dd/MM/yyyy"
                        value={formData.delivery_date}
                        placeholder='dd/mm/yy'
                        onChange={onInputChange}
                    />
                </div>
                <div className="modalForm">
                    <label htmlFor="price">Price*</label>
                    <span>
                        <input
                            type="number"
                            placeholder='0.00'
                            name="price"
                            id="price"
                            value={formData.price}
                            required
                            onChange={onInputChange}
                        />
                    </span>
                </div>
                <div className="modalForm">
                    <label htmlFor="commodityDescription">Commodity Description*</label>
                    <input
                        type="text"
                        required
                        name="description"
                        // name="commodityDescription"
                        // id="commodityDescription"
                        id="description"
                        value={formData.description}
                        onChange={onInputChange}
                    />
                </div>
                
                <button className='detailsbtn' type='submit'><FaChevronRight />
                    Proceed
                </button>
            </form>
        </div>
     );
}

export default Details;