import { faEdit } from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState,useEffect } from "react";
import FormModal from "./modalFolder/transactionModal";
// import { NavLink } from "react-router-dom";

const TransactionButton = (prop)=>{

    const [showModal, setShowModal] = useState(false);

  const handleNewTransaction = () => {
    setShowModal(true);
  };
  const onClose = ()=>{
    console.log('overlay')
    setShowModal(false)
  }
    return (
        <div className="dashHead">
            <h2 className="remove">{ prop.head }</h2>
            <button className='dashHeadButton' onClick={handleNewTransaction}>
              {/* <NavLink to='/admin/createTransaction'> */}
                <FontAwesomeIcon icon={faEdit} size='2x' /> <span className="remove">New Transaction</span> 
              {/* </NavLink> */}
            </button>
      {showModal && <FormModal  onClose={onClose} setShowModal={setShowModal} />}
      </div>
    )
}
export default TransactionButton;