
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faComments,faBell  } from "@fortawesome/free-regular-svg-icons";
import {  FaUserAlt } from "react-icons/fa";
import { NavLink, Navigate, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { useLogoutMutation } from "context/usersApiSlice";
import {logout} from '../context/authSlice'

function TopBar() {
    const navigate = useNavigate();

    const {userInfo} = useSelector((state)=> state.auth)
    const [logoutApiCall] = useLogoutMutation();
    const dispatch = useDispatch()

    const handleLogout = async () => {
        // localStorage.removeItem("token");
        try {
          // await logoutApiCall().unwrap();
          dispatch(logout())
          navigate("/reg/login");
        } catch (error) {
          console.log(error)
        }
      };

      const dropdown = () => {
        if (userInfo) {
          return (
            <div className="dropdown">
                <button className="dropbtn">{userInfo.user.firstName } {userInfo.user.lastName} <FaUserAlt color="#007538" size='20px'/></button>
                <div className="dropdown-content">
                    <NavLink to="/admin/settings/profile">Profile</NavLink>
                    <button onClick={handleLogout}>Logout</button>
                </div>
             </div>
          );
        } else {
          return <p>Log in to see your profile</p>;
        }
      };

    return ( 
        <div className="TopBar">
           <div className="searchDiv">
                <input type="text" placeholder=" Search" />
           </div>
           <div className="topRHS">
            <button className="topShape user">
                {/* {userImg ? <FaUserAlt color="#007538" size='20px'/> : <FaAccessibleIcon/>} */}
                {dropdown()}
            </button>
                <div className='topbarCTAs'>   
                    <div className="topShape"> 
                        <span></span>
                        <FontAwesomeIcon icon={faComments} size="lg" color="#007538" />
                    </div>
                    
                    <div className="topShape"> 
                        <span></span> 
                        <FontAwesomeIcon icon={faBell} size="lg" color="#007538" />
                    </div>
                </div> 
           </div>
        </div>
     );
}

export default TopBar;